body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

table, th, td {
    border:1px solid black;
}
.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .modal-content {
    background-color: #fff;
    padding: 20px;
    border-radius: 5px;
  }
  
  .close {
    /* position: fixed; */
    margin-left: 260px;
    cursor: pointer;
  }
  
/* Style the select element */
select,
input {
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 4px;
    background-color: white;
    color: #333;
    width: 200px;
}


select::-ms-expand {
    display: none;
}

select::after {
    content: '\25BC';
    position: absolute;
    top: 50%;
    right: 10px;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    font-size: 14px;
    pointer-events: none;
}

/* Style the select options */
select option {
    background-color: white;
    color: #333;
}

/* Style the select on hover */
select:hover,
input:hover {
    border-color: #999;
}
.login-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 100px;
}

.login-form {
  display: flex;
  flex-direction: column;
  width: 300px;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.form-group {
  margin-bottom: 10px;
}

label {
  font-weight: bold;
}

.form-input {
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 3px;
}

button {
  padding: 8px 12px;
  color: #fff;
  background: red;
  border: 1px solid red;
  border-radius: 20px;
  cursor: pointer;
}

button:hover {
  background-color: red;
}
.widget {
    text-align: center;
}

.widget.feel-safe.full-width {
    height: auto;
    padding: 60px 0;
}

.widget.feel-safe {
    background-color: #36312A;
    color: #FFF;
    position: static;
    height: 380px;
}

.widget.feel-safe.full-width .container .global-typography .heading-3 {
    text-align: center;
    font-size: 22px;
    line-height: 1.3;
    text-transform: uppercase;
    letter-spacing: .05em;
}
